.manage_wrap {
	height: 100%;

	.container {
		display: flex;
		height: 100%;
		width: 100%;
		flex-direction: column;
	}

	.class_header {
		display: flex;
		padding: 10px;
		border-radius: 8px;
		border: 1px solid #ddd;
		background: #FAFAFA;
		align-items: center;

		strong {
			padding-right: 15px;
		}

		.date {
			margin-right: 20px;
		}

		.time {

		}

		.reservation {
			margin-left: auto;
		}

		.btn {
			font-size: 12px;
			height: 32px;
			padding: 0 8px;
			border: 1px solid $color-gray-d9;
			border-radius: 4px;

			& ~ .btn {
				margin-left: 7px;
			}

			&.finish {
				color: $color-main;
				border-color: $color-main;
			}
		}
	}

	.manage_header {
		display: flex;
		font-size: 16px;
		line-height: 32px;
		align-items: center;
		flex: 0 0 auto;

		.sort_list {
			margin-left: auto;
		}

		.btn_edit {
			height: 32px;
			margin-left: auto;
			padding: 0 8px;
			font-weight: 400;
			font-size: 12px;
			border: 1px solid #D9D9D9;
			border-radius: 2px;

			svg {
				margin-right: 4px;
				vertical-align: -2px;
			}
		}
	}

	.manage_content {
		position: relative;
		flex: 1 1 auto;
		padding-top: 12px;

		.personal_list {
			margin: 0 -16px;
			padding: 10px 0;
			box-sizing: border-box;

			@include mq(tablet) {
				margin: 0;
				border: 0;
				padding-top: 0;
			}

			&:empty {
				height: calc(100% - 48px);
				text-align: center;

				&::before {
					content: '일정이 없습니다.';
					display: inline-block;
					text-align: center;
					vertical-align: middle;
					color: #666;
				}

				&:after {
					content: '';
					display: inline-block;
					width: 0;
					height: 100%;
					vertical-align: middle;
				}
			}

			.item {
				position: relative;

				&:hover {
					background-color: #E9F7FE;
				}

				.inner {
					display: flex;
					padding: 0 8px 0 16px;
					line-height: 40px;
					align-items: center;
				}

				a {
					@include absoluteFull;
				}

				& ~ .item {
					margin-top: 16px;
				}
			}

			.index {
				width: 36px;
				padding-right: 10px;
				box-sizing: border-box;
			}

			.date {
				min-width: 115px;
				height: 40px;
				text-align: justify;

				&:after { content:""; display:inline-block; width:100%; height: 0; vertical-align: top}
			}

			.state {
				padding: 0 16px;
				font-size: 11px;
				color: #999;

				&.prearrange {
					color: #25AAF5;
				}
			}

			.btn_detail {
				position: relative;
				z-index: 20;
				display: inline-block;
				line-height: 24px;
				padding: 0 4px;
				font-size: 12px;
				color: #666;
				border: 1px solid #D9D9D9;
				background-color: #fff;
				border-radius: 2px;
				vertical-align: 1px;
				box-sizing: border-box;

				&:hover {
					background-color: #f7f7f7;
				}
			}

			.ic_arrow {
				flex: 1 1 auto;
				text-align: right;

				svg {
					vertical-align: -6px;
				}
			}
		}
	}

	.manage_ticket {
		overflow: hidden;
		margin-top: 10px;
		padding: 14px 16px;
		border: 1px solid #e1e1e1;
		border-radius: 2px;
		background-color: #fafafa;
		font-size: 12px;
		line-height: 22px;

		.ticket_info {
			float: left;
			width: 50%;
			word-break: keep-all;

			dt {
				float: left;
				font-weight: 700;
			}

			dd {
				overflow: hidden;
				padding-left: 8px;
			}

			.highlight {
				color: #25AAF5;
			}
		}
	}

	.btn_back {
		margin-right: 4px;
	}

	.class_description {
		overflow: auto;
		min-height: 200px;
		margin-top: 30px;
		margin-bottom: auto;
		padding: 16px;
		border: 1px solid #999;
		border-radius: 5px;
		resize: none;
	}

	.btn_modify {
		//margin-top: auto;
		flex: 0 0 auto;
		margin-top: 20px;
		height: 44px;
		background-color: #25AAF5;
		color: #fff;
		font-size: 16px;

		&:disabled {
			background-color: #c9c9c9;
		}
	}
}