body {

}

#wrap {
	margin: 0 auto;

	@include mq(pc) {
		max-width: 1030px;
		padding: 20px 0;
	}

	@include mq(tablet) {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 100%;
	}
}

.container {
	height: 100%;

	@include mq(tablet) {
		overflow: auto;
		flex: 1 1 auto;
		height: auto;
		background: #fff;
	}

	h2 {
		font-size: 20px;
		line-height: 24px;
	}

	.wrap {
		display: flex;
		height: 100%;
		flex-direction: column;
	}

	.scroll_area {
		display: flex;
		flex-direction: column;
		overflow: auto;
		flex: 1 1 auto;
		@include scrollHide;
	}

	.notify_area {
		flex: 0 0 auto;
		padding: 16px;
		border-bottom: 1px solid #eee;
		background-color: #fff;
		box-sizing: border-box;

		@include mq(tablet) {
			display: none;
		}
	}

	.section {
		padding: 16px;
		background-color: #fff;
		line-height: 19px;

		& > [class*="_header"] {
			padding-bottom: 10px;
			border-bottom: 1px solid #ddd;
		}

		@include mq(tablet) {
			margin-top: 0;
			box-shadow: none;
		}

		&.full {
			display: flex;
			flex-direction: column;
			flex: 1 1 auto;
			height: 100%;
			box-sizing: border-box;
		}

		h3 {
			font-size: 20px;
			line-height: 24px;
		}

		.title {
			display: block;
			font-size: 16px;
			line-height: 22px;
		}

		.description {
			margin-top: 8px;
			color: #666;
		}
	}

	.article {
		display: flex;
		align-items: center;

		.article_content {
			flex: 1 1 auto;
			padding-right: 16px;
		}

		.btn_article {
			flex: 0 0 auto;
			margin-left: auto;
			@include g-button(40);
		}
	}
}
