.schedule_wrap {
	overflow-y: auto;
	height: 100%;

	@include scrollHide;
	scroll-behavior: smooth;

	&.type_trainer {
		.calendar_default_event,
		.calendar_default_cell,
		.calendar_default_cell_inner {
			pointer-events: none !important;
		}

		.calendar_default_shadow_inner {
			background-color: transparent !important;
		}
	}

	.tab {
		position: relative;
		margin-top: -20px;
		text-align: center;
		font-size: 18px;
		line-height: 48px;

		.menu_area {
			display: inline-block;
			margin-left: 8px;
		}

		.btn_menu {
			vertical-align: 1px;

			&.open ~ .ly_menu {
				display: block;
			}
		}

		.ly_menu {
			display: none;
			position: absolute;
			left: 50%;
			top: 100%;
			z-index: 10;
			width: 200px;
			margin-top: -10px;
			padding: 10px 0;
			border-radius: 5px;
			transform: translateX(-50%);
			background: #fff;
			text-align: left;
			box-shadow: 5px 5px 10px 0px rgba(0,0,0,0.1);

			.menu {
				display: block;
				padding: 0 15px;
				line-height: 40px;
				font-size: 14px;

				&:hover {
					background-color: #fafafa;
				}

				&.active {
					font-weight: 700;
				}
			}
		}
	}

	.list_area {
		padding: 16px;
		border: 1px solid #E1E1E1;
		background-color: #FAFAFA;
	}

	.control_area {
		padding-bottom: 10px;

		.index {
			font-size: 12px;
			padding-right: 12px;
		}
	}

	.person_list {
		@include clearfix;
		margin-top: -14px;

		.item {
			float: left;
			width: 20%;
			margin-top: 14px;
			line-height: 20px;
		}
	}

	.modal {
		.day_list {
			color: #666;

			.item {
				display: flex;
				height: 52px;
				align-items: center;
				font-size: 16px;

				& ~ .item {
					margin-top: 4px;
				}
			}

			.text {
				padding-right: 16px;
			}

			.btn_manage {
				height: 38px;
				margin-left: auto;
				padding: 0 14px;
				color: #25AAF5;
				border: 1px solid currentColor;
				border-radius: 2px;
			}
		}
	}
}

.calender_wrap {
	position: relative;
	margin-top: 24px;

	.btn_add {
		position: absolute;
		top: 0;
		right: 0;
		color: #666;
		height: 28px;
		padding: 0 14px;
		border: 1px solid #d9d9d9;
		font-size: 12px;
		border-radius: 2px;

		svg {
			margin-right: 6px;
			vertical-align: -4px;
		}
	}
}

.mbsc-calendar {
	max-height: 500px;
}

.modal {
	.schedule_add_area {
		.plus_area {
			margin: 0 -16px 20px;
			padding: 12px 0 12px 16px;
			background: #F5F5F5;

			@include clearfix;
		}

		.btn_plus {
			float: left;
			width: 70px;
			height: 70px;
			margin-right: 8px;
			background: #fff;
			border: 1px solid #E1E1E1;
			border-radius: 2px;
		}

		.plus_list {
			overflow-x: auto;
			white-space: nowrap;
			color: #666;

			.item {
				position: relative;
				display: inline-block;
				width: 157px;
				height: 70px;
				margin-right: 8px;
				padding: 9px;
				font-size: 11px;
				line-height: 12px;
				vertical-align: top;
				background-color: #fff;
				border: 1px solid #E1E1E1;
				box-sizing: border-box;
				cursor: pointer;

				&.selected {
					border-color: #25AAF5;
				}

				.name {
					font-weight: 700;
					color: #333;

					& + .text {
						margin-top: 10px;
					}
				}

				.text {
					margin-top: 5px;
				}

				.btn_delete {
					position: absolute;
					top: 8px;
					right: 8px;
					line-height: 0;

					&:hover {
						path {
							fill: #25AAF5;
						}
					}
				}

				.btn_copy {
					position: absolute;
					top: 8px;
					right: 26px;
					line-height: 0;

					&:hover {
						rect:not(:first-child) {
							stroke: #25AAF5;
						}

						path {
							stroke: #25AAF5;
						}
					}
				}
			}
		}

		.plus_input_area {
			display: flex;

			& ~ .plus_input_area {
				margin-top: 20px;
			}

			&.required {
				label {
					&::after {
						content: '';
						display: inline-block;
						width: 4px;
						height: 4px;
						margin-left: 2px;
						border-radius: 50%;
						background-color: #FF4B50;
						vertical-align: 9px;
					}
				}
			}

			label {
				padding-right: 10px;
				width: 60px;
				line-height: 36px;
				box-sizing: border-box;
			}

			.input {
				flex: 1 1 auto;
				height: 36px;
				padding: 0 10px;
				border: 1px solid #D9D9D9;
				border-radius: 5px;
				box-sizing: border-box;
			}

			.time {
				padding: 0;

				.rc-time-picker-input {
					height: 100%;
					border: 0;
					font-size: 14px;
					color: #333;
					padding: 0 10px;
				}

				.rc-time-picker-clear {
					display: none;
				}
			}

			select {
				@include apperanceNone;

				&:disabled {
					background-color: #E1E1E1;
				}
			}

			.textarea {
				resize: none;
				min-height: 100px;
				padding: 10px;
			}

			.dash {
				padding: 0 10px;
				font-size: 0;
				line-height: 36px;

				&::before {
					content: '';
					width: 14px;
					height: 1px;
					background-color: #333;
					display: inline-block;
				}
			}
		}

		.btn_add {
			margin-top: 16px;
			width: 100%;
			height: 36px;
			border-radius: 2px;
			border: 1px solid #25AAF5;
			color: #25AAF5;
		}

		.sub_footer {
			.btn_default {
				width: 160px;
				flex: 0 0 auto;
				margin-right: 8px;
			}

			.btn_point {

			}
		}
	}
}

.time_select_layer {
	width: 195px;

	.rc-time-picker-panel-input-wrap {
		display: none;
	}

	.rc-time-picker-panel-inner {
		transform: translateY(39px) !important;
	}
}

.flicking-viewport {
	overflow: hidden;
	white-space: nowrap;

	.flicking-panel {
		display: inline-block;
		width: 100%;
		vertical-align: top;
	}
}