.input_label {
	display: inline-block;
	color: #999;
	vertical-align: top;

	.text {
		display: inline-block;
		color: #333;
	}

	&::before {
		content: '';
		display: inline-block;
		width: 16px;
		height: 16px;
		margin-right: 8px;
		border: 2px solid currentColor;
		vertical-align: -3px;
		box-sizing: border-box;
	}
}

.input_check {
	@include blind;

	&[type='radio'] {
		& ~ .input_label {
			&::before {
				border-radius: 50%;
			}
		}


		&[checked] ~ .input_label {
			&::before {
				//background-color: currentColor;
				border-width: 5px;
			}
		}
	}

	&[type='checkbox'] {
		& ~ .input_label {
			&::before {
				border-radius: 2px;
			}
		}

		&[checked] ~ .input_label {
			&::before {
				background: url("data:image/svg+xml,%3Csvg width='11' height='9' viewBox='0 0 11 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4.26667L4.19796 7.58307C4.40981 7.80277 4.76735 7.78306 4.95377 7.5414L10 1' stroke='white' stroke-width='1.5' stroke-linecap='round'/%3E%3C/svg%3E%0A") no-repeat center;
				background-color: currentColor;
			}
		}
	}
}