.home_wrap {
	.register {
		padding: 20px;
		border-radius: 8px;
		border: 1px solid #ddd;
	}

	.member_header {
		.num {
			color: #25AAF5;
		}
	}

	.member_list {
		margin: 0 -20px;

		.item {
			@include mq(tablet) {
				padding: 0 20px;
			}

			a {
				display: flex;
				flex-direction: row;
				align-items: center;
				height: 40px;
				margin-top: 10px;
				padding: 0 20px;

				@include mq(tablet) {
					padding: 0 ;
				}

				&:hover {
					background-color: rgba(#E9F7FE, 0.8);
				}
			}
		}

		.date {
			padding-left: 12px;
			font-size: 11px;
			color: #666;
		}

		.btn_edit {
			padding: 0 14px;
			margin-left: auto;
			height: 26px;
			border: 1px solid #D9D9D9;
			border-radius: 2px;
			font-size: 12px;
			color: #666;
		}

		.arrow {
			margin-left: auto;
		}

		.reservation {
			margin-left: auto;
			padding-left: 12px;
			font-weight: 700;

			&.wait {
				color : #25AAF5;

			}
		}
	}
}