.aside {
	float: left;
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 250px;
	margin-right: 16px;
	padding: 5px 20px;
	background-color: #fff;
	box-sizing: border-box;

	@include mq(tablet) {
		float: none;
		height: auto;
		width: 100%;
		padding: 0;
		margin: 0;
	}

	.aside_header {
		padding: 35px 0;
		flex: 0 0 auto;
		text-align: center;

		@include mq(tablet) {
			padding: 0;

		}

		.company {
			margin-bottom: 30px;

			@include mq(tablet) {
				margin: 0;
				border-bottom: 1px solid #ddd;
			}

			a {
				display: flex;
				height: 55px;
				align-items: center;
				justify-content: center;
			}
		}

		.logo_admin {
			margin-left: 8px;
		}

		.profile {
			@include mq(tablet) {
				display: flex;
				padding: 17px 42px;
				align-items: center;
			}
		}

		.thumb_area {
			position: relative;
			width: 68px;
			height: 68px;
			margin: 0 auto;

			@include mq(tablet) {
				flex: 0 0 auto;
				width: 56px;
				height: 56px;
				margin: 0;
			}

			.input_file {
				@include blind;
			}

			.label_file {
				position: absolute;
				top: 0;
				right: 0;
				z-index: 1;
				width: 20px;
				height: 20px;
				padding-top: 4px;
				border-radius: 50%;
				background-color: rgba(#333, 0.9);
				box-sizing: border-box;
			}
		}

		.thumbnail {
			overflow: hidden;
			width: 100%;
			height: 100%;
			position: relative;
			border-radius: 50%;

			&::after {
				content: '';
				@include absoluteFull;
				border-radius: 50%;
				border: 1px solid #d9d9d9;
			}

			img {
				position: absolute;
				top: -50%;
				left: -50%;
				bottom: -50%;
				right: -50%;
				margin: auto;
				width: 100%;
				height: auto;
			}
		}

		.information {
			margin-top: 16px;

			@include mq(tablet) {
				display: flex;
				flex: 1 1 auto;
				height: 100%;
				margin-top: 0;
				align-items: center;
			}

			.name_area {
				overflow: hidden;
				position: relative;

				&.edit {
					.name {
						visibility: hidden;
					}
					.input_name {
						display: block;
					}
				}
			}

			.name,
			.input_name {
				display: block;
				width: 100%;
				padding: 0 20px;
				font-weight: 700;
				font-size: 16px;
				line-height: 24px;
				outline: none;
				text-align: center;
				-webkit-appearance: none;
				resize: none;
				word-break: break-all;
				word-wrap: break-word;
				box-sizing: border-box;

				@include mq(tablet) {
					width: auto;
				}
			}

			.input_name {
				display: none;
				@include absoluteFull;
				text-align: left;
				text-decoration: underline;
				text-decoration-color: #25AAF5;
			}

			.btn_edit {
				@include absoluteVerticalCenter;
				right: 0;
				width: 16px;
				height: 16px;
				padding-top: 4px;
				border-radius: 50%;
				background-color: rgba(#333, 0.9);
				box-sizing: border-box;
			}

			.description {
				margin-top: 4px;
				color: #333;
				font-size: 11px;
				line-height: 15px;
			}

			.btn_setting {
				margin-top: 12px;
				padding: 0 8px;
				border: 1px solid #acacac;
				border-radius: 2px;
				font-size: 12px;
				line-height: 30px;

				@include mq(tablet) {
					margin: 0 0 0 auto;
				}

				svg {
					vertical-align: -2px;
				}
			}
		}
	}

	.aside_content {
		flex: 1 1 auto;
		padding-top: 16px;
		border-top: 1px solid #d9d9d9;

		@include mq(tablet) {
			padding-top: 0;
			border-top: 0;
		}

		.menage_list {
			margin: 0 -20px;
			color: #999;

			@include mq(tablet) {
				margin: 0;
			}

			.item {
				@include mq(tablet) {
					float: left;
					width: 50%;

					& ~ .item {
						.main_link {
							border-left: 1px solid #E1E1E1;
						}
					}
				}

			}

			.navigation {
				display: flex;
				flex-direction: column;
			}

			.main_link {
				padding: 0 20px;
				order: 0;
				font-size: 16px;
				line-height: 60px;

				@include mq(tablet) {
					position: relative;
					height: 54px;
					text-align: center;
					border-bottom: 1px solid #E1E1E1;

					&.active {
						border-color: transparent;

						&::before {
							display: block;
						}
					}

					&::before {
						content: '';
						display: none;
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						height: 2px;
						background: #222;
					}
				}

				@include mq(pc) {
					&:hover {
						background-color: #E9F7FE;
					}
				}
			}

			.active {
				font-weight: 700;
				color: #25aaf5;

				@include mq(pc) {
					background-color: #E9F7FE;
				}

				svg path {
					fill : #25aaf5;
				}

				& ~ .main_link {
					font-weight: 700;

					@include mq(pc) {
						color: #25aaf5;
						background-color: #E9F7FE;
					}

					@include mq(tablet) {
						color: #333;
						border-color: transparent;

						&::before {
							display: block;
						}
					}

					svg path {
						fill : currentColor;
					}
				}
			}

			.sub_link {
				order: 1;
				padding-left: 64px;
				font-size: 14px;
				line-height: 35px;

				@include mq(pc) {
					&:hover {
						background-color: #E9F7FE;
					}
				}

				@include mq(tablet) {
					padding: 0;
				}

				&.hidden {
					@include blind;
				}
			}

			svg {
				margin-right: 20px;
				vertical-align: -7px;
			}
		}
	}
}