.calender_weekday_wrap {
	.period_header {
		text-align: center;
	}

	.btn_prev {
		svg {
			transform: rotate(90deg);
		}
	}
	.btn_next {
		svg {
			transform: rotate(-90deg);
		}
	}

	.period {
		padding: 0 12px;
		font-size: 18px;
		line-height: 22px;
	}

	.btn_today {
		position: absolute;
		top: 0;
		left: 0;
		color: #666;
		height: 28px;
		padding: 0 14px;
		border: 1px solid #d9d9d9;
		font-size: 12px;
		border-radius: 2px;
	}
}

#wrap {
	.calendar_default_main {
		margin-top: 35px;
		border: 0;

		& > div:nth-child(2) {
			//height: 500px !important;
			height: calc(100vh - 150px) !important;
		}

		tr {
			tr  {
				&:nth-child(odd) {
					.calendar_default_cell_inner {
						border-bottom: 0;
					}
				}
			}
		}

		td {
			& > div {
				width: 100%;
			}
		}

		.calendar_default_colheader_inner,
		.calendar_default_corner_inner,
		.calendar_default_rowheader_inner,
		.calendar_default_cell_inner {
			//border-width: 0 0 1px 0;
			background-color: transparent;
			border-color: #E1E1E1;
		}

		.calendar_default_shadow {
			[class*='inner'] {
				margin: 0 2px 2px 0;
				background-color: #E1E1E1;
			}
		}

		.calendar_default_rowheader_inner {
			border-width: 0 1px 0 0;
			padding: 0 10px 0 0;
			font-size: 10px;
			line-height: 12px;
			text-align: left;
			color: #666;

			& > div {
				margin-top: -2px;
			}

			.calendar_default_rowheader_minutes{
				font-size: 10px;
				vertical-align: top;
			}
		}

		.calendar_default_colheader_inner {
			flex-direction: column;
			justify-content: start;
			border-width: 0 0 1px 0;
			text-align: center;

			.day {
				font-size: 10px;
				line-height: 12px;

				&.today {
					color: #25AAF5;
				}
			}

			.num {
				display: inline-block;
				margin-top: 8px;
				width: 32px;
				height: 32px;
				color: #333;
				font-size: 20px;
				font-weight: 400;
				line-height: 32px;
				vertical-align: top;

				&.today {
					border-radius: 50%;
					background-color: #25AAF5;
					color: #fff;
				}
			}
		}

		.calendar_default_corner_inner {
			border: 0;
		}

		.calendar_default_cell_inner {
			border-width: 0 1px 1px 0;
		}


		.calendar_default_event_bar {
			//display: none;
		}

		.calendar_default_event {
			cursor: pointer !important;
		}

		.calendar_default_event_inner {
			position: relative;
			display: flex;
			flex-direction: column;
			height: 100%;
			padding: 0 8px;
			border: 0;
			background: #5784FC;
			font-size: 10px;
			line-height: 12px;
			color: #fff;
			white-space: normal;
			justify-content: center;
			border-radius: 4px;
			font-weight: 700;
			box-sizing: border-box;

			&::after {
				content: '';
				position: absolute;
				top: 5px;
				right: 5px;
				width: 5px;
				height: 5px;
				border-radius: 50%;
				background: #fff;
			}

			&[style*=' rgb(255, 255, 255)'] {
				text-shadow: -1px 0px rgba(0, 0, 0, 0.05), 0px 1px rgba(0, 0, 0, 0.05), 1px 0px rgba(0, 0, 0, 0.05), 0px -1px rgba(0, 0, 0, 0.05);
			}

			.name {
				font-size: 12px;
				line-height: 14px;
			}

			.time {

			}
		}
	}
}
